var iconNames = [
    'android',
    'appIcon',
    'arrow',
    'attention',
    'auto24',
    'bubble',
    'bubbles',
    'close',
    'delete',
    'edit',
    'facebook',
    'finance24',
    'flag/at',
    'flag/be',
    'flag/de',
    'flag/es',
    'flag/fr',
    'flag/it',
    'flag/lu',
    'flag/nl',
    'flag/pl',
    'googleplus',
    'heart',
    'hook',
    'immo24',
    'info',
    'ios',
    'location',
    'mail',
    'phone',
    'pin',
    'pinCar',
    'pinMoto',
    'pinterest',
    'search',
    'sharing',
    'star-half',
    'star',
    't-online',
    'tip',
    'twitter',
    'whatsapp',
    'youtube'
];

var icons = {};

iconNames.forEach(function(name) {
    icons[name.toLowerCase()] = require('../icons/' + name + '.svg');
});

var proto = Object.create(HTMLElement.prototype);

proto.createdCallback = function() {
    this.innerHTML = icons[('' + this.getAttribute('type')).toLowerCase()];
};

proto.attributeChangedCallback = function(attributeName, previousValue, value) {
    if (attributeName === 'type') {
        this.innerHTML = icons[('' + this.getAttribute('type')).toLowerCase()];
    }
};

document.registerElement('as24-icon', { prototype: proto });



